
import VueDB from '../../util/vue-db/vue-db-long';
import headerBack from "../../components/header-back";
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


var DB = new VueDB();
export default{
data(){
return {
params:'',
headTitle:'所有店铺',
storeHeader:"../../../static/images/classStore/storeHeader.jpg",
storeItem1:"../../../static/images/classStore/storeItem1.jpg",
storeItem2:"../../../static/images/classStore/storeItem2.jpg",
storeList:[],
}
},
mounted(){
this.params = this.$route.query.params || 'apple';
this.initData(this.params);
},
methods:{
initData(itemNo){
this.$http.get("/userLogin/queryClassStore").then((res) => {
var resData = res.data.data.data;
this.classData = resData;
this.cClassData(resData,itemNo); 
}).catch((err) => {
console.log(err)
})


},
cClassData(itemData,itemNo){
var _this = this;
var classData = itemData;
this.classData.forEach(function(item,index){
if(item.classId == itemNo){
_this.storeList = item.stores;
}
})
},
goClassification(link,param){
DB.setItem("qaStoreNo",{qaStoreNo:param});
this.$router.openPage(link);
}
},
components: {
Swiper,
SwiperItem,
headerBack,
XImg,
Divider
},
}



